@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.container {
    position: absolute;
    display: grid;
    grid-template-rows: auto auto;
    align-items: center;
    right: 0;
    top: 0;
    width: 35%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.1);
    mix-blend-mode: screen;
    backdrop-filter: blur(20px);
    font-family: 'Roboto', sans-serif;
}

h1 {
    display: flex;
    color: #fff;
    mix-blend-mode: difference;
    font-size: 26px;
    padding-left: 45px;
}

p {
    font-size: 18px;
    color: #fff;
}

hr {
    width: 90%;
}

.temperature-info {
    display: grid;
    grid-template-columns: 80% 20%;
    padding-left: 45px;
    align-items: center;
    line-height: 3;

}

.details-info {
    display: grid;
    grid-template-columns: 80% 20%;
    grid-template-rows: auto auto auto;
    padding-left: 45px;
    align-items: center;
    line-height: 3;

}

@media (max-width: 700px) {
    .container {
        width: 100%;
        height: 55%;
        top: auto;
        bottom: 0;
    }

    .temperature-info,
    .details-info {
        line-height: 1;
    }

    h1 {
        font-size: 18px;
    }

    p {
        font-size: 12px;
    }
}