@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.container-desc {
    position: absolute;
    left: 15%;
    bottom: 15%;
    width: 35%;
    height: 20%;
    display: grid;
    grid-template-columns: 35% 65%;
    grid-template-rows: 60% 40%;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.1);
    mix-blend-mode: screen;
    backdrop-filter: blur(20px);
    font-family: 'Roboto', sans-serif;
}

.temperature-main {
    font-size: 96px;
    grid-row: 1 / span 2;
    justify-self: center;
}

.city-name {
    font-size: 42px;
}

.weather-desc {
    font-size: 22px;
}

@media (max-width: 700px) {
    .container-desc {
        height: 15%;
        width: 90%;
        left: 5%;
        bottom: 60%;
        grid-template-columns: 45% 55%;
    }

    .temperature-main {
        font-size: 72px;
    }

    .city-name {
        font-size: 32px;
    }
}